.wrapper {
  margin: 50px 20px 30px 20px;
}
.header {
  font-family: 'Caviar Dreams';
  font-size: 22px;
  line-height: 36px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  font-weight: 300;
  margin: 0 0 15px 0;
}
.info {
  display: flex;
  flex-direction: column;
  width: 320px;
  margin: 0 auto;
}

.img {
  display: block;
  width: 320px;
  height: auto;
}

.text {
  order: -1;
  font-weight: 300;
  font-size: 14px;
  line-height: 24px;
  color: #FFFFFF;
  margin: 0 0 15px 0;
  list-style: none;
  padding-left: 16px;
}
.textItem {
  position: relative;
}
.textItem::before {
  display: block;
  position: absolute;
  top: 50%;
  left: -16px;
  transform: translate(0, -50%);
  content: '';
  width: 8px;
  height: 8px;
  background: #69C9CA;
  border-radius: 50%;
}
@media(min-width: 1200px) {
  .wrapper {
    margin: 80px 0 50px 70px;
    padding: 0 160px 0 50px;
    background-image: url(../../../img/background/seventhScreen.svg);
    background-repeat: no-repeat;
    background-size: 400px auto;
    background-position: 100% 30%;
  }

  .header {
    font-size: 48px;
    line-height: 60px;
    margin-bottom: 50px;
  }
  .info {
    flex-direction: row;
    width: 100%;
    order: unset;
  }
  .img {
    order: -1;
    margin-right: 100px;
    width: calc(100% - 100px);
    height: auto;
  }
  .text {
    order: unset;
    margin: 50px 0 0 0;
    width: 36%;
    font-size: 18px;
    line-height: 30px;
    padding-left: 37px;
  }
  .textItem:not(:last-child) {
    margin-bottom: 10px;
  }
  .textItem::before {
    width: 13px;
    height: 13px;
    left: -24px;
  }
}
